@import '../../assets/scss/color';
@import '../../assets/scss/fonts';





































































































































































































































































































































.rghMenu {
  // .mail-ruleForm {
  //   padding-left: 58px;
  //   .el-form-item__label {
  //     text-align: left;
  //  }
  // }
  .default {
    width: 90%;
    height: 240px;
    border: 1px solid $color-control;
    overflow: auto;
    letter-spacing: 1.2px;
    line-height: 25px;
  }

  .ambiance {
    background-color: #212121;
    color: #ffb795;
  }

  .mail_tag {
    margin: 5px 10px;
  }
  #test {
    &:focus {
      outline: 1px solid $color-main;
      border: 1px solid transparent !important;
    }
  }
  .el-dialog__header {
    height: 56px;
    padding: 16px 20px;
    box-sizing: border-box;
    background-color: $color-back-header !important;
  }
  .el-dialog .el-dialog__footer {
    height: 60px;
    padding: 12px 40px;
    -webkit-box-shadow: inset 0 1px 0 0 #dbdbdb;
    box-shadow: inset 0 1px 0 0 #dbdbdb;
  }
  .el-form-item {
    margin-bottom: 15px;
  }
  .rgh_ul,
  .rgh_ul_span {
    list-style: none;
    display: none;
    position: fixed;
    z-index: 9999;
    background: $color-back-header;
    padding-left: 0;
    border-radius: 4px;
  }
  .rgh_ul li,
  .rgh_ul_span li {
    padding: 0px 10px;
    font-size: 14px;
    height: 28px;
    line-height: 28px;
    // height: 40px;
    // width: 150px;
    // line-height: 40px;
    // text-align: center;
  }
  .rgh_ul_span li:first-child {
    border-bottom: 1px solid $color-table-cutting;
  }
  .rgh_ul li:hover,
  .rgh_ul_span li:hover {
    background: $color-form-hover;
    cursor: pointer;
    border-radius: 4px;
  }
  .slot_html {
    border: 1px solid $color-table-cutting;
  }
  .titleCls {
    width: 250px;
    .el-input__inner {
      background-color: transparent;
      // border: none;
      font-size: 18px;
      color: #303133;
    }
  }
}
.mail_poper {
  .el-tree {
    max-height: 400px;
    overflow-y: auto;
  }
}
