.functionModule {
  position: relative;
  top: -4.2px;
  line-height: 28px;
  height: 28px;
  min-width: 1px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
  .event_div {
    line-height: 28px;
    height: 28px;
    min-width: 70px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
  }
  .arguments_div {
    min-width: 20px;
    height: 28px;
    line-height: 26px;
    display: inline-block;
    vertical-align: middle;
    text-align: center;
    margin-right: 5px;
  }
  .input-special {
    vertical-align: middle;
    width: 140px;
    .el-input__inner {
      width: 140px;
    }
  }
}
