@import '../../../../../../assets/scss/color';
@import '../../../../../../assets/scss/fonts';




























































































































































































































































































.wrapper {
  padding: 0 5px;
  .itemBox {
    position: relative;
    min-height: 60px;
    line-height: 60px;
  }
  .item {
    position: relative;
    // width: 95%;
    // height: 40px;
    padding: 10px 20px;
    // line-height: 40px;
    color: #fff;
    cursor: move;
    border-left: 3px solid $color-back-card;
  }
  .activeItem {
    background: $color-form-hover;
    border-left: 3px solid $color-main !important;
  }
  .activeGroupItem {
    background: #ecf5ff;
    border: 0.5px solid $color-main !important;
  }
  .groupBox {
    position: relative;
    min-height: 68px;
    padding-bottom: 80px;
    .groupButton {
      position: absolute;
      bottom: -29px;
      right: 20px;
    }
  }
  .groupBox:hover {
    border: 1px dashed $color-main;
  }
  .itemCover {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    min-height: 62px;
    cursor: move;
  }
  .itemButton {
    position: absolute;
    right: 25px;
    bottom: -27px;
    z-index: 5;
  }
  .itemOperatButton {
    width: 30px;
    height: 30px;
    padding: 0;
    line-height: 30px;
    border: 1px solid #ccc;
    border-radius: 15px;
  }
  .copyButton {
    color: $color-main;
    background: #ecf5ff;
    border-color: #b3d8ff;
  }
  .copyButton:hover {
    color: #fff;
    background: $color-main;
  }
  .brushButton {
    color: #e6a23c;
    background: #fdf6ec;
    border-color: #f5dab1;
  }
  .brushButton:hover {
    color: #fff;
    background: #e6a23c;
  }
  .deleteButton {
    color: #f56c6c;
    background: #fef0f0;
    border-color: #fbc4c4;
  }
  .deleteButton:hover {
    color: #fff;
    background: #f56c6c;
  }
}
