@import '../../../../../../assets/scss/color';
@import '../../../../../../assets/scss/fonts';








































































































































































































































































































































































































































































































































































































































































































































































































































































































.custom-form {
  width: 100%;
  height: 650px;
}

.manualList {
  padding: 0;
  margin-top: 5px;
  .active {
    background-color: #eee;
  }
  li {
    list-style-type: none;
    height: 33px;
    line-height: 33px;
    font-size: 14px;
    color: #233a72;
    padding: 0 10px 0 20px;

    &:hover {
      background: #eaf1fe;
    }
  }

  .operateBtn {
    float: right;

    .el-button {
      font-size: 12px;
      color: #3674f9;

      + .el-button {
        margin-left: 10px;
      }
    }
  }
}

.active {
  background: #ecf5ff;
}
.form-box {
  position: relative;
  height: 100%;
  padding: 0px 345px 0px 136px;
  background: #f5f7fc;
  .left-toolbar {
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    z-index: 1;
    width: 130px;
    padding: 5px;
    overflow: auto;
    background-color: $color-back-card;
    border: 1px solid $color-control;
    border-radius: 5px;
    .tool-title {
      padding: 10px;
      margin-bottom: 10px;
      font-size: 16px;
      border-bottom: 1px solid $color-control;
      color: $color-subtitle-text;
    }
    .tool-item-title {
      font-size: 12px;
      color: $color-main-text;
      margin-bottom: 6px;
    }
    .dragItem {
      width: 108px;
      height: 30px;
      padding: 0 10px;
      margin-bottom: 6px;
      font-size: 12px;
      line-height: 30px;
      color: $color-main-text;
      cursor: move;
      background: $color-back-header;
      border: 1px dashed $color-control;
      .itemTitle {
        margin-left: 10px;
      }
    }
    .dragItem:hover {
      border: 1px dashed $color-main;
    }
  }
  .center-dropArea::-webkit-scrollbar,
  .right-attribute::-webkit-scrollbar,
  .left-toolbar::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 4px; /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
  }
  .center-dropArea::-webkit-scrollbar-thumb,
  .right-attribute::-webkit-scrollbar-thumb,
  .left-toolbar::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius: 4px;
    // box-shadow   : inset 0 0 5px rgba(64, 158, 255, 0.2);
    background: $color-icon;
  }
  .center-dropArea::-webkit-scrollbar-track,
  .right-attribute::-webkit-scrollbar-track,
  .left-toolbar::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    // box-shadow   : inset 0 0 5px rgba(64, 158, 255, 0.2);
    border-radius: 4px;
    background: $color-control;
  }
  .center-dropArea {
    width: 100%;
    height: 100%;
    background-color: $color-back-card;
    border-radius: 5px;
    overflow: auto;
  }
}
