.eventRegisterOptions {
  .left {
    float: left;
    width: 1100px;
    padding-left: 10px;
    margin-top: 0;
  }
  .listli {
    .delicon {
      margin-left: 5px;
      font-size: 12px;
      color: #3674f9;
      visibility: hidden;
      cursor: pointer;
    }
    &:hover {
      .delicon {
        visibility: visible;
      }
    }
  }

