@import '../../../../../assets/scss/color';
@import '../../../../../assets/scss/fonts';


















































































































































































































































































































.mailDialog {
  .mail_tag {
    margin: 5px 10px;
  }
  #test {
    &:focus {
      outline: 1px solid #409dfe;
      border: 1px solid transparent !important;
    }
  }
  .el-dialog__header {
    height: 56px;
    padding: 16px 20px;
    box-sizing: border-box;
    background-color: rgba(13, 63, 153, 0.1) !important;
  }
  .el-dialog .el-dialog__footer {
    height: 60px;
    padding: 12px 40px;
    -webkit-box-shadow: inset 0 1px 0 0 #dbdbdb;
    box-shadow: inset 0 1px 0 0 #dbdbdb;
  }
  .el-form-item {
    margin-bottom: 15px;
  }

  .titleCls {
    width: 250px;
    .el-input__inner {
      background-color: transparent;
      // border: none;
      font-size: 18px;
      color: #303133;
    }
  }
}
.inputCon {
  max-width: 200px;
}
