@import '../../../../assets/scss/color';
@import '../../../../assets/scss/fonts';




























































































































































































































































































































































































































































































































































































































































































.basicInfo {
  .title {
    padding-left: 30px;
    line-height: 49px;
    margin-top: -10px;
    color: #606266;
    font-size: 16px;
  }
  .desc {
    color: #606266;
    padding: 0 5px;
  }
  .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
  .timeoutContainer {
    .el-input {
      width: 200px;
    }
  }
  // popover弹出框按钮
  .popoverBtn {
    padding: 5px 15px;
    margin-left: 10px;
  }
  .el-input {
    width: 300px;
  }
  .el-textarea {
    width: 600px;
  }
  .reminderCon {
    display: inline-block;
    width: 50%;
    vertical-align: top;
  }
}
