@import '../../../../../assets/scss/color';
@import '../../../../../assets/scss/fonts';

































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































.ruleEditor {
  .basicContent {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;

    .content {
      height: auto;
      max-height: 400px;
    }
    .content.basicQueryLeft {
      height: auto;
      overflow-y: auto;
      max-height: 400px;
    }

    .basicQueryLeft {
      width: 30%;
    }
    .basicQueryRight {
      flex: 1;
      margin-left: 20px;
      width: 70%;

      .custom-tree-node {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        padding-right: 8px;

        .marginRt {
          margin-right: 3px;
        }
      }
    }
  }
}
